import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { useCartContext } from '../context/CartContext';

const Home = () => {
  // state to store product list 
  const[products , setProducts] = useState([]);
  const[categories , setCategories] = useState([]);
  const {cart , setCart} = useCartContext();
  console.log(cart);
  const navigate = useNavigate()

  // state to handle selected categories
  const [checked , setChecked]  =useState([]);
  console.log(checked);

  // state to handle filtered products
  const [filtered , setFiltered] = useState([]);
  console.log(filtered);
  // fuction to get all products
  const getAllProd = async()=>{
    try {
      const res = await axios.get('/api/v1/products/get-products');
      if(res.data.success){
        console.log(res.data);
        setProducts(res.data.getProducts);
      }else{
        window.alert("not product available thank you")
      }
    } catch (error) {
      console.log(error);
      window.alert("error in fetching products")
    }
  }

  const getCategories = async()=>{
    try {
      const res = await axios.get("/api/v1/category/get-categories");
      if(res.data.success){
        // console.log(res.data);
        setCategories(res.data.categories);
      }else{
        window.alert("not product available thank you")
      }
    } catch (error) {
      console.log(error);
      window.alert("error in fetching categories")
    }
  }
  // function to handle filter
  const handleFilter =()=>{
    const filter = products.filter((p)=> p.category.includes() );
    setFiltered(filter);
  }

  useEffect(()=>{
    getAllProd();
    getCategories();
    if(!cart){
      setCart([]);
    }
    
  },[]);

  useEffect(()=>{
    handleFilter();
    console.log("i am in");
  },[checked])
  return (
    <div className='container-fluid'>
      <div className="row">
        <div className="col-md-3 "> 
        <h4>filter by categories</h4>
        {categories ? categories.map((c)=> <div className="form-check" onClick={()=>setChecked([...checked , c._id])}>
        <input className="form-check-input" type="checkbox" name='category' value={c._id} id={c._id}/>
        <label className="form-check-label" for={c._id}>
          {c.name}
        </label>
      </div> ) :""}
        </div>
        <div className="col-md-9">
          <h4>products</h4>
          <div className="row">
          {products ? products.map((prod)=>{
            return <div className="col-md-4"> <div className="card" style={{"width" : "95%" ,'height' : "20rem"}}>
            <img src={`/api/v1/products/prodImage/${prod._id}`} className="card-img-top" alt="..." style={{'width' :'100%' , 'height' : "10rem"}} />
            <div className="card-body">
              <h5 className="card-title">{prod.name}</h5>
              <p className="card-text">{prod.description}</p>
              <div className='d-flex justify-content-between'><button  className="btn btn-primary" onClick={()=>navigate(`/product/${prod.slug}`)}> More details</button>
              <button  className="btn btn-primary" onClick={()=> {setCart([...cart , prod]); window.alert("product added to cart")}}> Add to cart</button></div>
              
            </div>
          </div></div>
          }) : "loading..."}
          </div>
        </div>
      </div>
    </div>
  )
}

export default Home
