import React from 'react'
import { Link } from 'react-router-dom'

const PageNotFound = () => {
    return (
      <div className='container'>
        <div className="row my-5">
          <div className="col-sm-10 text-center text-secondary" style={{"margin" : "auto"}}>
              <h1 className='pghead'>
                  404
              </h1>
              <h2 className='pgsubhead'>
                  OOPs you are lost...
              </h2>
              <p className='mt-4 pgp'>The page you are looking for does not exist . How you got here is a mystry. But you can click the buttton below to go back to home page. </p>
              <Link to={'/'}> <button className="btn text-white mb-3" style={{"background" : "#8AAAE5"}}> Go back to Home </button></Link>
          </div>
        </div>
      </div>
    )
  }

export default PageNotFound
