import React from 'react'
import { Link } from 'react-router-dom'

const UserMenue = () => {
  return (
    <div>
      <h1 className='text-center'>user panel</h1>
    <ul className="list-group">
      <li className="list-group-item" aria-current="true"><Link to={'/orders'}>Orders</Link></li>
      <li className="list-group-item"><Link to={'/Profile'}>Profile</Link></li>
    </ul>
  </div>
  )
}

export default UserMenue
