import Footer from "./component/Footer";
import Navbar from "./component/Navbar";
import Home from './pages/Home'
import About from './pages/About'
import {Routes , Route, useLocation} from 'react-router-dom'
import Policy from "./pages/Policy";
import Contact from "./pages/Contact";
import PageNotFound from "./pages/PageNotFound";
import Register from "./pages/Register";
import Login from "./pages/Login";
import SEO from "./component/SEO";
import Dashboard from "./pages/Dashboard";
import PrivateRoute from "./privateroute/PrivateRoute";
import AdminRegister from "./pages/AdminRegister";
import AdminLogin from "./pages/AdminLogin";
import CreateCategory from "./component/CreateCategory";
import CreateProducts from "./component/CreateProducts";
import Userss from "./component/Userss";
import Order from "./component/Order";
import Profile from "./component/Profile";
import Products from "./component/Products";
import UpdateProduct from "./component/UpdateProduct";
import ProductDetails from "./pages/ProductDetails";
import Cart from "./pages/Cart";


function App() {
  const path = useLocation().pathname;
  let title = '';
  let description = '';

  switch (path) {
    case '/register':
      title = "Register";
      description = "register yourself"
      break;
  
    case '/login':
      title = "login";
      description = "login yourself"
      break;
    case '/contact':
      title = "Contact";
      description = "login yourself"
      break;
    case '/policy':
      title = "Policy";
      description = "Our policies are the best policies" 
      break;
    case '/cart':
      title = "cart";
      description = " shop more "
      break;
    case '/dashboard':
      title = "dashboard";
      description = " user details "
      break;
      
    default:
      title= "Ecommerce app - shop now";
      description = "mern stack project"
  }
  
  return (
    <>
    <Navbar/>
    <SEO title={title}  description={description} />
    <Routes>
      <Route path="/" element ={ <Home/> } />
      <Route path='/product/:slug' element={<ProductDetails/>}/>
      <Route path="/about" element ={ <About/> } />
      <Route path="/policy" element ={ <Policy/> } />
      <Route path="/contact" element ={ <Contact/> } />
      <Route path="/cart" element ={ <Cart/> } />
      <Route path="/register" element ={ <Register/> } />
      <Route path="/register-admin" element ={ <AdminRegister/> } />
      <Route path="/login" element ={ <Login/> } />
      <Route path="/login-admin" element ={ <AdminLogin/> } />
      {/* protecting dashboard route */}
      <Route path="/dashboard" element={<PrivateRoute/>}>
        <Route path="" element ={ <Dashboard/> } />
        <Route path="create-category" element={<CreateCategory/>} />
        <Route path="create-product" element={<CreateProducts/>} />
        <Route path="products" element={<Products/>} />
        <Route path="products/:slug" element={<UpdateProduct/>} />
        <Route path="users" element={<Userss/>} />
      </Route>
      {/* <Route path="/dashboard/create-category" element={<CreateCategory/>} /> */}
      {/* <Route path="/dashboard/create-product" element={<CreateProducts/>} />
      <Route path="/users" element={<Userss/>} /> */}
      <Route path="/orders" element={<Order/>} />
      <Route path="/profile" element={<Profile/>} />
      <Route path="*" element ={ <PageNotFound/> } />
    </Routes>
    <Footer/>
    </>
  );
}

export default App;
