import React from 'react'

const FormTocreateCategory = ({handleSubmit , name , setName , edit}) => {
  return (
    <form onSubmit={handleSubmit}>
    <div className="mb-3">
        <input type="text" placeholder='enter category name to create ' value={name} className="form-control" onChange={(e)=> setName(e.target.value)} />
    </div>
    
    <button type="submit" className="btn btn-primary">{edit ? "edit" : "create"}</button>
    </form>
  )
}

export default FormTocreateCategory
