import axios from 'axios';
import React, { useEffect, useState } from 'react'
import Card from './Card';

const Products = () => {
    const [products , setProducts] = useState([]);
    const getAllProducts =async()=>{
        try {
            const res = await axios.get('/api/v1/products/get-products');
            if(res?.data?.success){
                setProducts(res.data.getProducts);
                console.log(products);
            }else{
                window.alert("no product found")
            }
        } catch (error) {
            console.log(error);
            window.alert("error in getting products")
        }
    }
    useEffect(()=>{
        getAllProducts();
        // eslint-disable-next-line
    } , [])
  return (
    <div className='container-fluid'>
      <div className="row">
        {products ? products.map((prod)=>{
            return <div className='col-sm-4 col-md-3'>  <Card id={prod._id} slug={prod.slug} name={prod.name} description={prod.description}/> </div>
        }): <h3>no product available</h3>}
      </div>
    </div>
  )
}

export default Products
