import React, { useEffect, useState } from 'react'
import { useAuthContext } from '../context/AuthContext'
import { useNavigate } from 'react-router-dom';
import { useCartContext } from '../context/CartContext';
import DropIn from "braintree-web-drop-in-react";
import axios from 'axios'

const Cart = () => {
    const {authUser} = useAuthContext();
    const {cart , setCart, cartTotal} = useCartContext();
    console.log(cartTotal);
    console.log(cart);

    const [clientToken, setClientToken] = useState("");
    const [instance, setInstance] = useState("");
    const [loading, setLoading] = useState(false);
    const navigate = useNavigate();

    // funtion to delete cart itm
    const removeItm = (id)=>{
      const filterData = cart.filter((c)=> id!== c._id);
      setCart(filterData);
    }

     //get payment gateway token
      const getToken = async () => {
        try {
          const { data } = await axios.get("/api/v1/products/braintree/token" , {headers : {authorization : authUser ? authUser.token : null}});
          setClientToken(data?.clientToken);
          console.log(data);
        } catch (error) {
          console.log(error);
        }
      };
    
    useEffect(()=>{
        const cartIms = localStorage.getItem("cart");
        setCart(JSON.parse(cartIms));
    },[]);
    useEffect(() => {
      getToken();
    }, [authUser?.token]);

    //handle payments
  const handlePayment = async () => {
    try {
      setLoading(true);
      const { nonce } = await instance.requestPaymentMethod();
      const { data } = await axios.post("/api/v1/products/braintree/payment", {
        nonce,
        cart,
      } , {headers : {authorization : authUser ? authUser.token : null}});
      console.log(data);
      setLoading(false);
      localStorage.removeItem("cart");
      setCart([]);
      navigate("/orders");
      window.alert("Payment Completed Successfully")
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  };
  return (
    <div className='container-fluid'>
      <h1>{authUser?.token ? `Hello ${authUser.userDetailes.name}` : "please Login first"}</h1>
      <div className="row">
        <div className="col-sm-7">
            {cart ? cart.map((cartItm)=> <div className="row border m-2">
                <div className="col-5" style={{'height' : "10rem"}}> <img src={`/api/v1/products/prodImage/${cartItm._id}`} alt="" style={{'height' : '100%' , 'width' :"100%"}} /> </div>
                <div className="col-7 text-center"> 
                    <h4>Name : {cartItm.name}</h4>
                    <h4>description : {cartItm.description}</h4>
                    <h4>Price : {cartItm.price}</h4>
                    <button className='btn btn-danger my-2'onClick={()=>removeItm(cartItm._id)}>Remove</button>
                </div>
            </div>) : "Your Cart is Empty" }
            
        </div>
        <div className="col-sm-5">
            <div className='row'>
                <h3>Cart Summary</h3>
                <h4>Total : Rs {cartTotal}</h4>
            </div>
            <div className="mt-2">
              {!clientToken || !cart?.length ? (
                ""
              ) : (
                <>
                  <DropIn
                    options={{
                      authorization: clientToken,
                      paypal: {
                        flow: "vault",
                      },
                    }}
                    onInstance={(instance) => setInstance(instance)}
                  />

                  <button
                    className="btn btn-primary"
                    onClick={handlePayment}
                    disabled={loading || !instance || !authUser?.userDetailes?.address}
                  >
                    {loading ? "Processing ...." : "Make Payment"}
                  </button>
                </>
              )}
            </div>
        </div>
        <div className="row">
            <button className='btn btn-primary' onClick={()=> {setCart([]) ; localStorage.setItem('cart' , JSON.stringify([]))}}> clear cart</button>
        </div>
      </div>

    </div>
  )
}

export default Cart
