import React from 'react'
import { Link } from 'react-router-dom'

const AdminMenue = () => {
  return (
    <div>
      <ul className="list-group">
        <li className="list-group-item" aria-current="true"><Link to={'/dashboard/create-category'}>Create Category</Link></li>
        <li className="list-group-item"><Link to={'/dashboard/create-Product'}>Create Products</Link></li>
        <li className="list-group-item"><Link to={'/dashboard/products'}>Products</Link></li>
        <li className="list-group-item"><Link to={'/dashboard/Users'}> Users </Link></li>
    </ul>
    </div>
  )
}

export default AdminMenue
