import React, { useEffect, useState } from 'react'
import AdminMenue from './AdminMenue'
import { useNavigate, useParams } from 'react-router-dom';
import { useAuthContext } from '../context/AuthContext';
import axios from 'axios';

const UpdateProduct = () => {
    const [productDetails , setProdDetails] = useState({});
    console.log(productDetails);
    // const [categories , setCategories] = useState([]);
    const [image , setImage] = useState('');
    const [categories, setCat] = useState([]);
    const [id ,setId] = useState();
    console.log((id));
    console.log(image);
    const{ authUser} = useAuthContext();
    const navigate = useNavigate();
    const {slug} = useParams();
    console.log(slug);

    const handleOnchage= (e)=>{
        let {name , value} = e.target;
        setProdDetails({...productDetails , [name] : value});
        console.log(productDetails);
    }
    
    const handelSubmit =async()=>{
    try {
        const productData = new FormData();
        productData.append("name", productDetails.name);
        productData.append("description",productDetails.description);
        productData.append("price", productDetails.price);
        productData.append("stock", productDetails.stock);
        productData.append("image", image);
        productData.append("category", productDetails.category);
        console.log(productData);
        const res = await axios.put(`/api/v1/products/updateProd/${productDetails._id}` , productData, {headers : {authorization : authUser ? authUser.token : null}});
        if(res?.data?.success){
        window.alert("product updated successfully");
        navigate('/dashboard/products')
        }else{
        window.alert("product not updated successfully");
        }
    } catch (error) {
        console.log(error);
        window.alert("errror in updating product")
    }
    }

    // fetching all caategory 
    const getingCategory = async()=>{
        try {
            const res = await axios.get("/api/v1/category/get-categories");
            if (res.data.success) {
              setCat(res.data.categories);
            }
          } catch (error) {
            console.log(error);
            window.alert(error);
          }
    }
    const getSingleProduct = async()=>{
        try {
            
            const res = await axios.get(`/api/v1/products/singleprod/${slug}`);
            console.log(res);
            if (res.data.success) {
                setProdDetails(res.data.singleprod);
                console.log(id);
                console.log(productDetails);
              }
        } catch (error) {
            console.log(error);
            window.alert("error in getting single product")
        }
    }

    // deleting product

    const deleteProd = async()=>{
        try {
            const res = await axios.delete(`/api/v1/products/deleteProd/${productDetails._id}`);
            if(res.data){
                window.alert("deleted successfuly");
                navigate('/dashboard/products')
            }else{
                window.alert("not deleted ")
            }
        } catch (error) {
            console.log(error);
            window.alert("error in deleting single product")
        }
    }

    useEffect(()=>{
        getSingleProduct();
        getingCategory();
        

        // eslint-disable-next-line
    },[])
    
  return (
    <div className="container-fluid">
      <div className="row">
        <h3 className="text-center">Update Products</h3>
        <div className="col-3">
          <AdminMenue />
        </div>
        <div className="col-9">
          
            <div className="col-md-6">
              <input type="text" className="form-control" name="name" value={productDetails.name} onChange={handleOnchage} placeholder="Name of your product"  />
            </div>
            <div className="col-md-6">
            <select id="inputState" name="category" required onChange={handleOnchage} className="form-select">
             
              {categories? categories.map((c)=><option key={c._id} name="category" value={c._id} > {c.name} </option>) : ""}
              </select>
            </div>
            <div className="col-12">
            <textarea className="form-control" name="description" value={productDetails.description} onChange={handleOnchage} placeholder="Description..." rows="3"></textarea>
            </div>
            <div className="col-md-4">
              <input type="number" className="form-control" name="stock" value={productDetails.stock}  onChange={handleOnchage} placeholder="Quantity" />
            </div>
            <div className="col-md-4">
              <input type="number" className="form-control" name="price" value={productDetails.price} onChange={handleOnchage} placeholder="Price" />
            </div>
            <div className="col-md-4">
              <label className="text-center form-control border" style={{'width' : "100%" , 'cursor' : "pointer"}}>
                {image.name ? image.name : 'upload image'}
              <input type="file" name="image"  accept="image/*" hidden onChange={(e)=>setImage(e.target.files[0])}/>
              </label>
            </div>
            <div className="col-12">
              {image?.name &&<img src={ URL.createObjectURL(image)} alt="product_photo" width={"200px"} height={"100px"} className="img img-responsive "/>}

               { productDetails._id ? !image  && <img src={productDetails ? `/api/v1/products/prodImage/${ productDetails._id}` : ""}/> : null}
            
            </div>
            <div className="col-12">
              <button type="submit" className="btn btn-primary mx-3" onClick={handelSubmit}>
                update product
              </button>
              <button type="submit" className="btn btn-danger" onClick={deleteProd}>
                delete
              </button>
            </div>
        </div>
      </div>
    </div>
  )
}

export default UpdateProduct
