import React, { useEffect, useState } from 'react'
import { useAuthContext } from '../context/AuthContext';
import { Outlet, useLocation } from 'react-router-dom';
import axios from 'axios'
import Spinner from '../component/Spinner';
const PrivateRoute = () => {
    const [ok , setOk] = useState();
    const{ authUser,  setLocation} = useAuthContext();
    const location = useLocation().pathname;
    setLocation(location);//setting the current location '/dashboard' so that we can use it in login to redirect te user directly to dashboard page if he was tring to access it before login

    useEffect(()=>{
      const checkAuthuser = async()=>{
        const res = await axios.get("/api/v1/auth/user-dashboard" , {headers : {authorization : authUser ? authUser.token : null}} );
        console.log("response", res);
        if(res.data.ok){
          setOk(true);
        
        }else{
          setOk(false)
        }
      }
      if(authUser){checkAuthuser()}
      // eslint-disable-next-line
    } , [authUser ]);
  return (
    <div>
      {ok ? <Outlet/> : <Spinner/>}
    </div>
  )
}

export default PrivateRoute
