import React, { useEffect, useState } from 'react'
import UserMenue from './UserMenue'
import { useAuthContext } from '../context/AuthContext'
import axios from 'axios';

const Profile = () => {

  const {authUser ,setAuthUser} = useAuthContext();
  // console.log(authUser);
  // state to collect user info 
  const [user , setUser] = useState({name :"" , email :"" ,phone : "" , password : "" , address : ""});
  // console.log(user);

  const handleInput = (e)=>{
    let {name , value} = e.target;
    setUser({...user , [name] : value});
  }

  const handleSubmit =async(e)=>{
    e.preventDefault();
    const res = await axios.put('/api/v1/auth/update-profile' , user, {headers : {authorization : authUser ? authUser.token : null}});
    if(res?.data?.success){
      // setAuthUser({...authUser , userDetailes : res.data.updateProf});
      // setUser(res.data.updateProf)
     
      let ls = JSON.parse(localStorage.getItem('auth'));
      ls.userDetailes = res.data.updateProf;
      localStorage.setItem("auth" , JSON.stringify(ls));
       window.alert(res.data.message)
    }else{
      window.alert(" profile not updated")
    }
  }

  useEffect(()=>{
    console.log("useeffect");
    if(authUser?.userDetailes){
      setUser({...authUser.userDetailes , password : ""});
    }
  },[authUser.userDetailes])

  return (
    <div className='container-fluid'>
      <div className="row">
        <div className="col-3"> <UserMenue/> </div>
        <div className="col-9">
            <h3 className='text-center'>user Profiles</h3>
            <form onSubmit={handleSubmit}>
            <div className="mb-3">
              <label htmlFor="exampleInputEmail1" className="form-label">Email</label>
              <input value={user.email} name='email' required type="email" disabled className="form-control" id="exampleInputEmail1" onChange={handleInput} aria-describedby="emailHelp"/>
            </div>
            <div className="mb-3">
              <label htmlFor="exampleInputEmail1" className="form-label">Name</label>
              <input value={user.name} name='name' required type="text" className="form-control" id="exampleInputEmail1" onChange={handleInput} aria-describedby="emailHelp"/>
            </div>
            <div className="mb-3">
              <label htmlFor="exampleInputEmail1" className="form-label">Phone</label>
              <input value={user.phone} name='phone' required type="text" className="form-control" id="exampleInputEmail1" onChange={handleInput} aria-describedby="emailHelp"/>
            </div>
            <div className="mb-3">
              <label htmlFor="exampleFormControlTextarea1" className="form-label">Address</label>
              <textarea name='address' value={user.address} required className="form-control" onChange={handleInput} id="exampleFormControlTextarea1" rows="3"></textarea>
            </div>
            <div className="mb-3">
              <label htmlFor="exampleInputEmail1" className="form-label">password</label>
              <input value={user.password} name='password' required type="password" className="form-control" id="exampleInputEmail1" onChange={handleInput} aria-describedby="emailHelp"/>
            </div>
            <button type="submit" className="btn btn-primary">Update Profile</button>
            
          </form>
        </div>
      </div>
    </div>
  )
}

export default Profile
