import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'

const ProductDetails = () => {
    const {slug} = useParams();
    const [product , setProduct] = useState({});
    const [category , setCategory] = useState({});
    console.log(product);
    console.log(category);
    // function to fetch product details
    const singleProduct = async()=>{
        try {
            const res = await axios.get(`/api/v1/products/singleprod/${slug}`);
            if(res.data){
                setProduct(res.data.singleprod);
                const {data} = await axios.get(`/api/v1/category/singleCategory/${res.data.singleprod.category}`);
                if(data){
                    setCategory(data.signleCat);
                }
            }else{
                window.alert("no result found")
            }
        } catch (error) {
            console.log(error);
            window.alert("error in fetching single product details")
        }
    }

    useEffect(()=>{
        singleProduct();
    },[])
  return (
    <div className='container'>
      product details
      <div className="row">
        
        {product && <> <div className="col-sm-4" style={{'height' : "20rem"}}> <img src={`/api/v1/products/prodImage/${product._id}`} style={{'width' : "100%" , 'height' : "100%"}} alt=""  /> </div>
        <div className="col-sm-8">
            <h4>Name : {product.name} </h4>
            <h4>category : {category.name} </h4>
            <h4>description : {product.description} </h4>
            <h4>price : {product.price} </h4>
        </div></>}
        
      </div>
    </div>
  )
}

export default ProductDetails
