const { createContext, useState, useContext, useEffect } = require("react");

const authContext  = createContext();


const AuthProvider = (props)=>{
    const [authUser , setAuthUser] = useState({});
    const [curLocation , setLocation] = useState('/') ;
    
    useEffect(()=>{
        const localUsr = localStorage.getItem("auth");
        if(localUsr){ 
            setAuthUser(JSON.parse(localUsr));
        }
        // eslint-disable-next-line
    } , [])
    return <authContext.Provider value={{authUser , setAuthUser ,curLocation , setLocation}} >{props.children}</authContext.Provider> 
}

// custom hook
const useAuthContext = ()=> useContext(authContext);

export default AuthProvider;
export {useAuthContext , authContext}