import axios from 'axios';
import React, { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom';
import { useAuthContext } from '../context/AuthContext';

const Login = () => {
  // getting authUSer details
  const {authUser, setAuthUser ,curLocation , setLocation}= useAuthContext();
  // console.log(authUser);
  const navigate = useNavigate()
   // state to collect user info 
   const [user , setUser] = useState({ email :"" , password : "" });

   // function to handle input
   const handleInput = (e)=>{
     let {name , value} = e.target;
     setUser({...user , [name] : value});
    //  console.log(user);
   }
   // function to handle submit action
   const handleSubmit= async(e)=>{
     e.preventDefault();
     try {
      const res = await axios.post('/api/v1/auth/login' , {...user});
      console.log(res);

      if(res.data.success){
        window.alert(res.data.message);
        localStorage.setItem("auth" , JSON.stringify(res.data));
        setAuthUser({...res.data , admin : false});
        console.log(authUser);
        navigate(curLocation);
        setLocation('/')
      }else{
        window.alert(res.data.message);
      }
     } catch (error) {
      console.log(error);
      window.alert("something went wrong plz try again latter.")
     }
   }
  return (
    <div className='container'>
      <div className='row justify-content-center align-items-center'>
        <div className='col-md-5 col-sm-8 col-10'>
          <form onSubmit={handleSubmit}>
            <h5>Login</h5>
            <div className="mb-3">
              <label htmlFor="exampleInputEmail1" className="form-label">Email</label>
              <input value={user.email} name='email' type="email" className="form-control" id="exampleInputEmail1" onChange={handleInput} aria-describedby="emailHelp"/>
            </div>      
            <div className="mb-3">
              <label htmlFor="exampleInputEmail1" className="form-label">password</label>
              <input value={user.password} name='password' type="password" className="form-control" id="exampleInputEmail1" onChange={handleInput} aria-describedby="emailHelp"/>
            </div>
            <button type="submit" className="btn btn-primary">Login</button>
            <br />
            <Link to={'/login-admin'}>Login as admin</Link>
          </form>
        </div>
      </div>
    </div>
  )
}

export default Login
