import React from 'react'
import { Link } from 'react-router-dom'

const Card = ({name , description , id ,slug}) => {
  return (
    <Link to={`/dashboard/products/${slug}`}>
    <div class="card" style={{"width": "100%"}}>
        <img src={`/api/v1/products/prodImage/${id}`} class="card-img-top" alt="..."/>
        <div class="card-body">
            <h5 class="card-title">{name}</h5>
            <p class="card-text">{description}</p>
        </div>
    </div>
    </Link>
  )
}

export default Card 