import React, { useState } from 'react'
import axios from 'axios'
import { Link, useNavigate } from 'react-router-dom';

const Register = () => {

  // useNavigate hook for navigating
  const navigate = useNavigate();

  // state to collect user info 
  const [user , setUser] = useState({name :"" , email :"" ,phone : "" , password : "" , address : ""});

  // function to handle input
  const handleInput = (e)=>{
    let {name , value} = e.target;
    setUser({...user , [name] : value});
    console.log(user);
  }
  // function to handle submit action
  const handleSubmit= async(e)=>{
    e.preventDefault();
    try {
      const res = await axios.post(`/api/v1/auth/register` , {...user});
      console.log(res);
      if (res.data) {
        window.alert(res.data.message);
        res.data.success ? navigate('/login') : navigate('/register')

      }
    } catch (error) {
      console.log(error);
      window.alert("something went wrong plz try again latter");
      navigate('/')
    }
    

  }
    return (
    <div className='container'>
      <div className='row justify-content-center align-items-center'>
        <div className='col-md-5 col-sm-8 col-10'>
          <form onSubmit={handleSubmit}>
            <h5>Register</h5>
            <div className="mb-3">
              <label htmlFor="exampleInputEmail1" className="form-label">Email</label>
              <input value={user.email} name='email' type="email" className="form-control" id="exampleInputEmail1" onChange={handleInput} aria-describedby="emailHelp"/>
            </div>
            <div className="mb-3">
              <label htmlFor="exampleInputEmail1" className="form-label">Name</label>
              <input value={user.name} name='name' type="text" className="form-control" id="exampleInputEmail1" onChange={handleInput} aria-describedby="emailHelp"/>
            </div>
            <div className="mb-3">
              <label htmlFor="exampleInputEmail1" className="form-label">Phone</label>
              <input value={user.phone} name='phone' type="text" className="form-control" id="exampleInputEmail1" onChange={handleInput} aria-describedby="emailHelp"/>
            </div>
            <div className="mb-3">
              <label htmlFor="exampleFormControlTextarea1" className="form-label">Address</label>
              <textarea name='address' value={user.address} className="form-control" onChange={handleInput} id="exampleFormControlTextarea1" rows="3"></textarea>
            </div>
            <div className="mb-3">
              <label htmlFor="exampleInputEmail1" className="form-label">password</label>
              <input value={user.password} name='password' type="password" className="form-control" id="exampleInputEmail1" onChange={handleInput} aria-describedby="emailHelp"/>
            </div>
            <button type="submit" className="btn btn-primary">Register</button>
            <Link to={'/register-admin'} >register as admin</Link>
          </form>
        </div>
      </div>
    </div>
  )
}

export default Register
