import React, { useEffect, useState } from 'react'
import UserMenue from './UserMenue'
import axios from 'axios'
import { useAuthContext } from '../context/AuthContext'
import moment from 'moment'
import Card from './Card'

const Order = () => {

  const {authUser} = useAuthContext();
  const [orderProf , setOrderProf] = useState([]);
  // function to get the order list 
  const orderList = async()=>{
    try {
      const res = await axios.get("/api/v1/auth/oderProfile" , {headers : {authorization : authUser ? authUser.token : null}});
      if(res?.data){
        console.log(res);
        console.log(res.data.orderlst);
        setOrderProf(res.data.orderlst)
      }else{
        window.alert("order is places yet")
      }
    } catch (error) {
      console.log(error);
      window.alert("there is error in order list")
    }
  }
  useEffect(()=>{
    orderList();
    // eslint-disable-next-line
  },[])
  return (
    <div className='container-fluid'>
      <div className="row">
        <div className="col-3"> <UserMenue/> </div>
        <div className="col-9">
            <h3> Orders </h3>
            <div>
              {orderProf && 
            <table class="table">
            <thead>
              <tr>
                <th scope="col">Sno</th>
                <th scope="col">Status</th>
                <th scope="col">Buyer</th>
                <th scope="col">date</th>
                <th scope="col">Payment</th>
                <th scope="col">Quantity</th>
              </tr>
            </thead>
            <tbody>
            {orderProf.map((odr , indx)=>{
              return <tr key={indx}>
              <th> {indx+1} </th>
              <td> {odr.status} </td>
              <td> {odr.buyer.name} </td>
              <td> { moment(odr.createdAt).fromNow() } </td>
              <td> {odr.payment.success ? "Done" : "pending"} </td>
              <td> {odr.products.length} </td>
            </tr>
            })}
            </tbody>
            { orderProf.map((odr)=>odr.products && odr.products.map((prod)=> <div style={{'height' : "20rem" , 'width' : "100%"}}> <Card name = {prod.name}  description = {prod.description} id = {prod._id} slug = {prod.slug} /> </div>) ) }
            </table>}
            </div>
        </div>
      </div>
    </div>
  )
}

export default Order
