import { Link, useLocation } from "react-router-dom";
import {FiShoppingCart} from "react-icons/fi"
import '../style/navbar.css'
import { useAuthContext } from "../context/AuthContext";
import { useCartContext } from "../context/CartContext";

const Navbar = () => {
  const {authUser , setAuthUser} = useAuthContext();
  // console.log(authUser);
  const handleLogout=()=>{
    localStorage.removeItem("auth");
    setAuthUser(null);

  }
 
   const{pathname}= useLocation();
   const {cart} = useCartContext();
  
  return (
    <div>
      <nav className="navbar navbar-expand-lg navbar-dark" style={{"background" : "#8AAAE5"}}>
        <div className="container-fluid">
            <Link className="navbar-brand" to={'/'}> <img src="./images/logo.png" style={{'width' : '8rem'}} alt="" /> </Link>
            <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse align-middle" id="navbarSupportedContent">
            <ul className="navbar-nav ms-auto mb-2 mb-lg-0 ">
                <li className="nav-item">
                <Link className={`nav-link ${pathname === "/" ? 'active' : null}`}  aria-current="page" to={'/'}>Home</Link>
                </li>

                <li className="nav-item">
                <Link className={`nav-link ${pathname === "/category" ? 'active' : null}`} aria-current="page" to={'/category'}>category</Link>
                </li>

                {authUser?.token ? <> <li class="nav-item dropdown">
          <a class="nav-link active dropdown-toggle" role="button" data-bs-toggle="dropdown" aria-expanded="false">
            {authUser?.userDetailes?.name}
          </a>
          <ul class="dropdown-menu bg-dark">
          <Link className={`nav-link ${pathname === "/dashboard" ? 'active' : null}`} to={`/dashboard`}  >Dashboard</Link>
            <Link className={`nav-link ${pathname === "/logout" ? 'active' : null}`} to={'/login'}  onClick={handleLogout} >Logout</Link>
          </ul>
        </li> </> : <> <li className="nav-item">
                <Link className={`nav-link ${pathname === "/register" ? 'active' : null}`} to={'/register'}>register</Link>
                </li>

                <li className="nav-item">
                <Link className={`nav-link ${pathname === "/login" ? 'active' : null}`} to={'/login'}>login</Link>
                </li> </>}
                <li className="nav-item cart">
                <Link to={'/cart'} type="button" className="nav-link position-relative">
                <FiShoppingCart className={` ${pathname=== '/cart' ? 'text-white fs-5' : 'text-white fs-6'}`}/>
                <span className="position-absolute top-10 start-150 translate-middle badge rounded-pill text-primary"> {cart? cart.length : 0} </span>
                </Link>

                </li>
            </ul>
            </div>
        </div>
    </nav>
    </div>
  );
};

export default Navbar;
