import React, { useEffect, useState } from "react";
import AdminMenue from "./AdminMenue";
import axios from "axios";
import { useAuthContext } from "../context/AuthContext";
import { useNavigate } from "react-router-dom";

const CreateProducts = () => {
  const [productDetails , setProdDetails] = useState({});
  const [categories , setCategories] = useState([]);
  const [image , setImage] = useState('');
  console.log(image);
  const{ authUser} = useAuthContext();
  const navigate = useNavigate();
 
  const handleOnchage= (e)=>{
    let {name , value} = e.target;
    setProdDetails({...productDetails , [name] : value});
    console.log(productDetails);
  }

  const handelSubmit =async()=>{

    console.log(productDetails);
    try {
      const productData = new FormData();
      productData.append("name", productDetails.name);
      productData.append("description",productDetails.description);
      productData.append("price", productDetails.price);
      productData.append("stock", productDetails.stock);
      productData.append("image", image);
      productData.append("category", productDetails.category);
      console.log(productData);
      const res = await axios.post('/api/v1/products/create-product' , productData, {headers : {authorization : authUser ? authUser.token : null}});
      if(res?.data?.success){
        window.alert("product created successfully");
        navigate('/dashboard/products')
      }else{
        window.alert("product not created successfully");
      }
    } catch (error) {
      console.log(error);
      window.alert("errror in creating product")
    }
  }

  const getallCat = async () => {
    try {
      const res = await axios.get("/api/v1/category/get-categories");
      if (res.data.success) {
        setCategories(res.data.categories);
        console.log(categories);
      }
    } catch (error) {
      console.log(error);
      window.alert(error);
    }
  };
  useEffect(()=>{
    getallCat()
    // eslint-disable-next-line
  },[])
  return (
    <div className="container-fluid">
      <div className="row">
        <h3 className="text-center">create Products</h3>
        <div className="col-3">
          <AdminMenue />
        </div>
        <div className="col-9">
          
            <div className="col-md-6">
              <input type="text" className="form-control" name="name" onChange={handleOnchage} placeholder="Name of your product"  />
            </div>
            <div className="col-md-6">
            <select id="inputState" name="category" onChange={handleOnchage} className="form-select">
              <option > select category </option>
              {categories? categories.map((c)=><option key={c._id} name="category" value={c._id} > {c.name} </option>) : ""}
              </select>
            </div>
            <div className="col-12">
            <textarea className="form-control" name="description" onChange={handleOnchage} placeholder="Description..." rows="3"></textarea>
            </div>
            <div className="col-md-4">
              <input type="number" className="form-control" name="stock"  onChange={handleOnchage} placeholder="Quantity" />
            </div>
            <div className="col-md-4">
              <input type="number" className="form-control" name="price" onChange={handleOnchage} placeholder="Price" />
            </div>
            <div className="col-md-4">
              <label className="text-center form-control border" style={{'width' : "100%" , 'cursor' : "pointer"}}>
                {image.name ? image.name : 'upload image'}
              <input type="file" name="image"  accept="image/*" hidden onChange={(e)=>setImage(e.target.files[0])}/>
              </label>
            </div>
            <div className="col-12">
              {image?.name &&<img src={ URL.createObjectURL(image)} alt="product_photo" width={"200px"} height={"100px"} className="img img-responsive "/>}
            
            </div>
            <div className="col-12">
              <button type="submit" className="btn btn-primary" onClick={handelSubmit}>
                create product
              </button>
            </div>
        </div>
      </div>
    </div>
  );
};

export default CreateProducts;
