import React, { useEffect, useState } from "react";
import AdminDash from "./AdminDash";
import AdminMenue from "./AdminMenue";
import axios from "axios";
import FormTocreateCategory from "./formTocreateCategory";
import { useAuthContext } from "../context/AuthContext";

const CreateCategory = () => {
  const [categories, setCat] = useState([]);
  const [name , setName] = useState();
  const [Catid , setId] = useState(); 
  const [edit , setEdit] = useState(false);
  const{ authUser} = useAuthContext();
  const getallCat = async () => {
    try {
      const res = await axios.get("/api/v1/category/get-categories");
      if (res.data.success) {
        setCat(res.data.categories);
      }
    } catch (error) {
      console.log(error);
      window.alert(error);
    }
  };
  const handleSubmit =async(e)=>{
    e.preventDefault();
    try {
      // this if block run when user wants to update the category
      if(edit){
        const res  = await axios.put(`/api/v1/category/update-category/${Catid}` , {name} ,{headers : {authorization : authUser ? authUser.token : null}});
        if(res.data.success){
          window.alert(`${name} updated successfuly`)
          getallCat();
          setEdit(false);
          setId();
          setName("");
        }else{
          window.alert(res.data.message);
        }
      }else{
          const res  = await axios.post('/api/v1/category/create-category' , {name} ,{headers : {authorization : authUser ? authUser.token : null}})
          if(res.data.success){
            window.alert(`${name} created successfuly`)
            getallCat()
          }else{
            window.alert(res.data.message);
          }
      }
      
    } catch (error) {
      console.log(error);
      window.alert("error" , error )
    }
  }

  // fucntion to delete the category
  const delCat = async(id)=>{
    try {
      const res = await axios.delete(`/api/v1/category/delete-category/${id}` , {headers : {authorization : authUser ? authUser.token : null}});

      if(res.data.success){
        window.alert("category deleted successfully");
        getallCat()
      }else{
        window.alert("not deleted")
      }
    } catch (error) {
      console.log(error);
      window.alert("error in deleting category" , error )
    }
  }
  useEffect(() => {
    getallCat();
  }, []);
  return (
    <div className="container-fluid">
      <div className="row">
      <h3 className="text-center">Manage Categories</h3>
        <div className="col-3">
          {" "}
          <AdminMenue />{" "}
        </div>
        <div className="col-9">
          {/* creating form to add new categories */}
          <FormTocreateCategory name={name} setName={setName} handleSubmit={handleSubmit} edit={edit} />
          {/* displaying existing categories */}
          <table class="table border">
            <thead>
              <tr>
                <th scope="col">Name</th>
                <th scope="col">Action</th>
              </tr>
            </thead>
            <tbody>
              {categories && categories.map((cat)=>{
                return <tr key={cat._id}>
                  <td> {cat.name} </td>
                  <td> <button onClick={()=>{setEdit(true) ; setName(cat.name) ; setId(cat._id)}} className="btn btn-primary me-2" >edit</button>  <button  className="btn btn-danger" onClick={()=> delCat(cat._id)} >delete</button> </td>
                </tr>
              })}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default CreateCategory;
